<template>
    <div class="page">
        <button>
        首页
    </button>
    <button @click="subPage">
        上一页
    </button>
    <button v-for="btn in pagebtns" :key=btn :class="[{'currentPage':btn===currentPage},'pagebtn']" @click="changeButton(btn)">
        {{btn}}
    </button>
    <button @click="addPage">
        下一页
    </button>
    </div>
</template>

<script>
export default {
    name:"Page",
    data(){
        return {
            pagebtns:[1,2,3,4,5,'......'],
            currentPage:1,
        }
    },
    methods:{
        changeButton(value){
            this.currentPage=value
            this.$emit('handleList',this.currentPage)
        },
        addPage(){
            if(this.currentPage===this.pagebtns[4]){
                this.pagebtns.shift()
                this.pagebtns.splice(4,0,this.pagebtns[3]+1)
            }
            this.currentPage++
            this.$emit('handleList',this.currentPage)
        },
        subPage(){
            if(this.currentPage===this.pagebtns[0]&&this.currentPage>1){
                this.pagebtns.splice(4,1)
                this.pagebtns.unshift(this.pagebtns[0]-1)
            }
            if(this.currentPage>1){
                this.currentPage--
                this.$emit('handleList',this.currentPage)
            }
        }
    }
}
</script>

<style scoped>
 .page {
    margin-top: 5px;
    margin-bottom: 20px;
    background-color: white;
    padding: 6px 20px;
    border-radius: 5px;
    /*box-shadow: 0px 2px 9px #888888;*/
    border: 1px solid #888888;
  }

  button {
    background-color: #fff;
    border: 1px solid #ddd;
    color: #778087;
    border-radius: 3px;
    outline: none;
    height: 21px;
    cursor: pointer;
    padding: 0 2px;
    width: 55px;
    height: 29px;
  }

  .pagebtn {
    position: relative;
    bottom: 1px;
    width: 40px;
    margin: 0 4px;
  }

  .currentPage {
    color: white;
    background-color: #1f1b1b;
  }

</style>


