</template>

<script>
export default {
    name:"Header"
}
</script>

<style>
.header{
    background-color: #5a5555;
    height:50px
}
.header>img{
    max-width:120px;
    margin-left:50px;
    margin-top:10px
}
.header>ul{
    list-style: none;
